import { Controller, Commands, Decoration } from '@ali/4ever-cangjie';

/**
 * 移动端取消时默认插入一个
 */
export default function removeMentionMark(controller: Controller) {
  const { value } = controller;
  const { decorations } = value;
  const newDecorations: Decoration[] = decorations.filter(
    (item) => item.mark.type !== 'mention',
  );
  return controller.setDecorations(newDecorations).command(Commands.focus);
}
